.product {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  display: flex;
}

.product__photo {
  margin-inline: auto;
  height: 134px;
  margin-bottom: 40px;
}

.product__title {
  color: #060b35;
  width: 166px;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.product__price-title {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.product__price-cost {
  color: #060b35;
  text-align: right;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.product__buy-button {
  justify-content: center;
  display: flex;
}

.product__buy-button-active {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .8s;
  display: flex;
}

.product__buy-button-active:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.product__stars-reviews {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.product__stars-reviews .stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.product__stars-reviews .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.product__stars-reviews .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product__stars-reviews .product__reviews {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  align-self: flex-end;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
}

